<template>
    <div class="invoice_container">
        <head-top head-title="选择发票抬头" goBack="ture"></head-top>
        <section class="invoice_fapiao">
            <div class="fapiao">
                <span>是否需要发票</span>
            </div>
            <div>
                <svg @click="chooseInvoice" :class="{choosed: invoice}">
                    <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#select"></use>
                </svg>
            </div>
        </section>
        <section class="invoice_anniou" @click="confrimInvoice">
            <span>确定</span>
        </section>
    </div>
</template>

<script>
import headTop from '../../../components/header/header'
import {mapMutations} from 'vuex'
export default {
    components: {
        headTop
    },methods: {
        ...mapMutations([
            'CONFIRM_INVOICE'
        ]),
        //是否需要发票
        chooseInvoice(){
            this.invoice=!this.invoice;
            console.log(this.invoice);
        },
        //保存发票信息
        confrimInvoice(){
            this.CONFIRM_INVOICE(this.invoice);
            this.$router.go(-1)
        }
    },props:['s1'],
    computed: {
    },data(){
        return{
            invoice:false
        }
    }
}
</script>

<style lang="scss" scopde>
@import '../../../style/mixin.scss';
.invoice_container{
    position: fixed;
    right: 0;
    left:0;
    top:0;
    bottom: 0;
    background: #f5f5f5;
    z-index: 103;
}
.invoice_fapiao{
    margin-top: 2.6rem;
    background-color: #fff;
    display: flex;
    justify-content: space-between;
    padding:0.5rem;
        align-items: center;
    svg{
        @include wh(0.8rem,0.8rem);
        padding-right: 0.3rem;
        fill: #666;
    }
    .choosed{
        fill: #4cd964;
    }
    .fapiao{
        @include sc(0.7rem,#666);
    }
}
.invoice_anniou{
    background:#4cd964;
    text-align: center;
    margin: 0.6rem;
    span{
        @include sc(0.7rem,#fff);
    }
    padding:0.2rem;
    border-radius: 0.2rem;
}
</style>



